@import url(https://fonts.googleapis.com/css?family=Ubuntu+Mono:400,700&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

body{background:#f2e4da}body .game-wrapper{display:flex;align-items:center;justify-content:center}body .game-wrapper .play-wrapper{align-self:flex-start}

.header{display:flex;align-items:center;justify-content:center;width:100%;height:90px;font-family:"Ubuntu Mono";font-size:40px;font-weight:bold;background:#97c78f}

.hidden{visibility:hidden}table{width:30vw;height:30vw;border-collapse:collapse}table .field-cell{border:1px solid grey}table .blue{background:blue}table .green{background:green}table .red{background:red}

.leaders-wrapper{margin-left:30px}.leaders-wrapper .leaders-table-wrapper{width:30vw;height:20vw;margin-top:20px;border-collapse:collapse;text-align:center}.leaders-wrapper .leaders-table-wrapper .leader-row-head{font-weight:bold}.leaders-wrapper .leaders-table-wrapper td,.leaders-wrapper .leaders-table-wrapper td{border:1px solid gray;vertical-align:middle}

form{font-family:"Ubuntu Mono"}

